.task_list {
    list-style-type: none;
    padding: 0;
}

.task_list_item {
    margin-bottom: 0.5em;
    background-color: #F6DDCE;
}

.clear_all {
    border-radius: 2px;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    color: white;
    background-color: #8B798C;
    font-size: 1.1em;
    font-weight: 300;
    text-transform: uppercase;
    text-decoration: none;
    padding: 0.35em 0.75em;
    border: none;
    cursor: pointer;
}

.clear_all:hover {
    background-color: #C17CCF;
}


